.client-headings{
    text-align: center;
}
.client-heading{
    font-size:var(--hero-text);
}


.logo-Section{
    width: 80%;
    height: 365px;
    background-color: transparent;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(6, 1fr);
    margin: 0 auto;
    margin-top:2rem;
}
.Com-logo{
    width: 12rem;
    height: 5rem;
    border-radius: 1rem;
    background-color: white;
    place-self: center;
    display:grid;
    place-items:center;
    box-shadow: 10px 10px 20px rgba(255, 255, 255, 0.247);
}
.logo-airbnd, .logo-amazon{
    grid-row: 1 /span 6;
}
.logo-Google, .logo-oyo{
    grid-row: 1 /span 3;
}
.logo-bookmyshow, .logo-ola{
    grid-row: 4 /span 3;
}
.logo-microsoft{
    grid-row: 1 /span 2;
}
.logo-fedex{
    grid-row: 3 /span 2;
}
.logo-walmart{
    grid-row: 5 /span 2;
}

@media (max-width: 500px){
    .logo-Section{
        width: 100%;
        height: 200px;
        margin: 0;
    }
    .Com-logo{
    width: 4rem;
    height: 2rem;
    }
    .Com-logo img{
        width: 2rem;
    }
}
