.hero{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 90vh;
    justify-content: space-between;
    overflow: hidden;
}
.hero-text{
    width: 50%;
    align-self: center;

}
.hero-text h1{
    font-size: var(--hero-text);
    font-weight: 700;
}
.hero-text .btn{
    margin-top: 1rem;
}
.charecter{
    width: fit-content;
    height: fit-content;
    background: linear-gradient(45deg,white , var(--clr-btn-bg), rgb(211, 92, 92));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}
.hero-illus{
    display: grid;
    place-items: center;
}
.gradient-circle{
    height: 350px;
    width: 350px;
    border-radius: 50%;
    background: linear-gradient(45deg,white , var(--clr-btn-bg), rgb(211, 92, 92));
    position: relative;
}
.img1,.img2{
    position: absolute;
    top: 0;
    left: 0;
    width: var(--figures-size);
}
.img1{
    transform: rotateY(180deg) translate(-100px, -100px);
}
.img2{
    transform: translate(-100px, 100px);
}
.img3, .img4{
    position: absolute;
    top: 0;
    left: 0;
    width: var(--other-sizes);
}
.img3{
    transform: translateY(-58px);
}
.img4{
    transform: translate(190px, 240px);
}

@media (max-width: 500px){
    .hero{
        flex-direction: column;
        height: auto;
        justify-content: flex-start;
        /* overflow:visible; */
        margin-bottom: 3rem;
        padding-bottom: 3rem;
    }
    .gradient-circle{
        width: 300px;
        height: 300px;
    }
    .hero .gradient-circle{
        margin-top: 5rem;
    }
    .hero-text{
        text-align: center;
        width: 80%;
        margin-bottom: 1rem;
    }
    .img2{
        transform: translate(-75px, 100px);
    }
}