@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@300&family=Pacifico&family=Poppins:wght@200&family=Roboto+Serif:wght@100&display=swap");

*,
::before,
::after {
  margin: 0;
  padding: 0;
}

:root {
  --clr-main-bg: #15083e;
  --clr-btn-bg: #8c72ff;
  /* font sizes */
  --hero-text : 3.6rem;
  --section-text : 2.5rem;
  --plain-text : 1.2rem;

  /* illus sizes */
  --figures-size : 350px;
  --other-sizes : 150px;
}

body {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  background-color: var(--clr-main-bg);
  color:white;
  overflow-x: hidden;
  font-size: var(--plain-text);

}
.main{
  max-width: 90vw;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.btn {
  padding: 0.7rem 1rem;
  color: white;
  background-color: var(--clr-btn-bg);
  border-radius: 2rem;
  cursor: pointer;
  border: none;
  transition: 500ms;
  font-size: 1rem;
}
.middle-text{
  text-align: right;
  font-size: var(--section-text);
  font-weight: 600;
  margin: 5rem 0;
  word-spacing: 5px;
  letter-spacing: 2px;
}
.bg-strip{
  width: 150%;
  height: 10rem;
  border: 5rem;
  position: absolute;
  top: 240%;
  left: -25%;
  background-color: var(--clr-btn-bg);
  transform: rotate(140deg);
  z-index: -1;
  opacity: 0.2;
}

@media (max-width: 500px){
  :root{
  --hero-text : 2rem;
  --section-text : 1.5rem;
  --plain-text : 0.8rem;
  --figures-size : 250px;
  --other-sizes: 100px;
  }
  .btn{
    font-size: 0.7rem;
  }
  .bg-strip{
    display: none;
  }
  .main{
    max-width: 100vw;
    margin: 0;
  }
  .middle-text{
    text-align: center;
    padding: .8rem;
  }
}