.feature-sec{
    display: flex;
    flex-direction: row;
    width: 100%;
    /* height: 20rem; */
    justify-content: space-evenly;
    margin: 13rem 0;
}
.flex-rev{
    flex-direction: row-reverse;
}
.section-text{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    max-width: 40%;
    padding: 1rem ;
    align-self: center;
    z-index: 10;
}
.section-text .btn{
    margin-top:1rem;
    max-width: fit-content;
}
.section-text h1{
    font-size: var(--section-text);
}
.section-text-head{
    text-transform: capitalize;
}
.sec-image{
    position: absolute;
    top: 0;
    bottom: 0;
    /* width: 350px; */
}
.sec1-img1,.sec2-img1{
    width: var(--figures-size);
    transform: translate(100px, 130px);
}
.sec1-img2, .sec1-img3, .sec2-img2, .sec2-img3{
    width: var(--other-sizes);
}
/* .sec1-img1{
    transform: translate(100px, 130px);
} */
.sec1-img2, .sec2-img2{
    transform: translate(160px, -100px);
}
.sec1-img3, .sec2-img3{
    transform: translate(-60px, 20px);
}

@media (max-width: 500px){
    .feature-sec{
        flex-direction: column;
        align-items: center;
        height: 76vh;
        justify-content: space-between;
        margin: 8rem 0;
    }
    .section-text{
        max-width: 90%;
        text-align: center;
    }
    .section-text .btn{
        margin: 0 auto;
    }
    .sec1-img2, .sec2-img2{
        transform: translate(160px, -30px);
    }
    .sec1-img3, .sec2-img3{
        transform: translate(-30px, 20px);
    }
}