.footer{
    width: 100%;
    padding-bottom: 3rem;
}
.footer-body{
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows:1fr ;
    place-items:start;
}
.Com-info{
    grid-column: 1 /span 2;
    padding-left: 4rem;
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.Com-info .btn {
    width:fit-content;
}
.Com-info p{
    line-height: 2.5;
}
.footer-list{
    line-height: 2.5;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding-top: 1rem;
    justify-self: flex-end;
}
.footer-list p a {
    text-decoration: none;
    color: white;
    font-weight: 600;
}
@media (max-width: 500px) {
    .Com-info{
        padding: 0;
        grid-column: 1 /span 3;
        gap: 0;
    }
    .footer-body{
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(3, 1fr);
    }
    .footer-list{
        gap: 0;
    }
}